import React from 'react';
import { useLocation, useNavigate } from 'react-router-dom';
import { useQuery } from '@tanstack/react-query';
import { useFormik } from 'formik';

import Box from '@mui/material/Box';
import Button from '@mui/material/Button';
import LocationOnIcon from '@mui/icons-material/LocationOn';
import LocalShippingIcon from '@mui/icons-material/LocalShipping';
import PersonIcon from '@mui/icons-material/Person';

import {
  Loader,
  SearchField,
  SearchMultiField,
  DateTimeField,
  SelectMultiField,
  TextField,
} from '../components';

import { LoadQuery, LocationsQuery, TrucksQuery, UsersQuery } from '../api';
import { useSnackbar } from '../hooks';
import {
  CreateLoad,
  Location,
  User,
  Truck,
  TruckTypes,
} from '../utils/dataTypes';

const initialValues = {
  id: '',
  pick: null,
  pickDate: null,
  deliver: null,
  deliverDate: null,
  weight: '',
  truckType: [],
  rate: undefined,
  bookedByUser: null,
  bookedByCompany: undefined,
  dispatchers: [],
  checkInAs: undefined,
  truck: undefined,
};

export default function ViewLoadForm() {
  const { pathname } = useLocation();
  const navigate = useNavigate();
  const setSnackbarMessage = useSnackbar();

  const {
    isLoading: loading,
    isFetching: fetching,
    data,
  } = useQuery({
    queryKey: ['load', pathname.split('/')[3]],
    queryFn: LoadQuery,
    keepPreviousData: true,
    onError: (error: Error) => {
      setSnackbarMessage({
        severity: 'error',
        message: error instanceof Error ? error.message : JSON.stringify(error),
      });
    },
  });

  const formik = useFormik<CreateLoad>({
    initialValues: {
      ...initialValues,
      ...data,
    },
    enableReinitialize: true,
    onSubmit: () => {
      return;
    },
  });

  const handleClose = () => {
    navigate(-1);
  };

  const handleEdit = () => {
    navigate(pathname.replace('view', 'edit'), { replace: true });
  };

  return (
    <Box>
      <form onSubmit={formik.handleSubmit} onReset={formik.handleReset}>
        <Box sx={{ p: 3, display: 'grid' }}>
          <Box key="controls" sx={{ display: 'inline-flex' }}>
            <Box key="left" sx={{ p: 1, width: '50%' }}>
              <SearchField<Location>
                fieldName="pick"
                label="Pick Location"
                getSelectedLabel={(selectedItem) =>
                  `${selectedItem.name}, ${selectedItem.zipCode}, ${selectedItem.stateName}`
                }
                getItemLabel={(item) => `${item.name}, ${item.stateCode}`}
                getItemSecondLabel={(item) =>
                  `${item.zipCode}, ${item.stateName}`
                }
                ListIcon={LocationOnIcon}
                queryFn={LocationsQuery}
                orderby="name"
                order="asc"
                disabled
                formik={formik}
              />
              <DateTimeField
                fieldName="pickDate"
                label="Pick Date"
                disabled
                formik={formik}
              />
              <TextField fieldName="weight" label="Weight" formik={formik} />
              <SelectMultiField
                fieldName="truckType"
                label="Truck Type"
                items={TruckTypes}
                disabled
                formik={formik}
              />
              <SearchField<User>
                fieldName="bookedByUser"
                label="Booked By"
                getSelectedLabel={(selectedItem) => `${selectedItem.fullName}`}
                getItemLabel={(item) => `${item.fullName}`}
                getItemSecondLabel={(item) => `${item.jobTitle}`}
                ListIcon={PersonIcon}
                queryFn={UsersQuery}
                orderby="fullName"
                order="asc"
                disabled
                formik={formik}
              />
            </Box>
            <Box key="right" sx={{ p: 1, width: '50%' }}>
              <SearchField<Location>
                fieldName="deliver"
                label="Deliver Location"
                getSelectedLabel={(selectedItem) =>
                  `${selectedItem.name}, ${selectedItem.zipCode}, ${selectedItem.stateName}`
                }
                getItemLabel={(item) => `${item.name}, ${item.stateCode}`}
                getItemSecondLabel={(item) =>
                  `${item.zipCode}, ${item.stateName}`
                }
                ListIcon={LocationOnIcon}
                queryFn={LocationsQuery}
                orderby="name"
                order="asc"
                disabled
                formik={formik}
              />
              <DateTimeField
                fieldName="deliverDate"
                label="Deliver Date"
                disabled
                formik={formik}
              />
              <TextField fieldName="rate" label="Rate" formik={formik} />
              <TextField
                fieldName="checkInAs"
                label="Check In As"
                disabled
                formik={formik}
              />
              <TextField
                fieldName="bookedByCompany"
                label="Booked By Company"
                disabled
                formik={formik}
              />
            </Box>
          </Box>
          <Box
            key="bottomControls"
            sx={{ marginTop: -2, marginBottom: 3, p: 1 }}
          >
            <SearchField<Truck>
              fieldName="truck"
              label="Truck"
              getSelectedLabel={(selectedItem) => `${selectedItem.truckNumber}`}
              getItemLabel={(item) => `${item.truckNumber}`}
              getItemSecondLabel={(item) =>
                `${item.status}, ${item.licencePlate}`
              }
              ListIcon={LocalShippingIcon}
              queryFn={TrucksQuery}
              orderby="truckNumber"
              order="asc"
              disabled
              formik={formik}
            />
            <SearchMultiField<User>
              fieldName="dispatchers"
              label="Dispatchers"
              getSelectedLabel={(selectedItem) => `${selectedItem.fullName}`}
              getItemLabel={(item) => `${item.fullName}`}
              getItemSecondLabel={(item) => `${item.jobTitle}`}
              ListIcon={PersonIcon}
              queryFn={UsersQuery}
              orderby="fullName"
              order="asc"
              disabled
              formik={formik}
            />
          </Box>
          <Box key="buttons" sx={{ display: 'inline-flex' }}>
            <Box key="left" sx={{ p: 1, paddingTop: 0, width: '50%' }}>
              <Button
                fullWidth
                variant="contained"
                size="small"
                color="primary"
                type="button"
                onClick={handleEdit}
              >
                Edit
              </Button>
            </Box>
            <Box key="right" sx={{ p: 1, paddingTop: 0, width: '50%' }}>
              <Button
                fullWidth
                variant="contained"
                size="small"
                color="primary"
                type="button"
                onClick={handleClose}
              >
                Cancel
              </Button>
            </Box>
          </Box>
        </Box>
      </form>
      <Loader visible={loading || fetching} />
    </Box>
  );
}
